.label {
  display: inline-block;
  margin-right: 20px;
}
.markdown a {
  transition: all 0.2s;
}
.markdown a:link,
.markdown a:visited,
.markdown a:hover,
.markdown a:active {
  text-decoration: none;
}
.markdown a:link,
.markdown a:visited {
  color: #00adb5;
}
.markdown a:hover,
.markdown a:active {
  color: #222831;
}
.markdown blockquote {
  border-left: 4px solid #00adb5;
  margin: 0;
  padding-left: 15px;
}
.markdown p img,
.markdown ul img {
  width: 100%;
}
.markdown p code,
.markdown ul code {
  border-radius: 5px;
  padding: 0px 6px;
  margin: 0px 3px;
  background-color: rgba(0, 0, 0, 0.1);
}
.markdown pre {
  border: 2px solid #00adb5;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  padding: 12px;
  overflow: auto;
}
